/* 0. Reset ================================= */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* 1. Main Layout ================================= */
* {
	box-sizing: border-box;
}

body {
	background: black;
}

h1 {
	font-family: 'Bowlby One SC', cursive;
	font-size: 7vw;
	width: 100vw;
	padding: 15px;
	text-align: center;
	z-index: 2;
	background: linear-gradient(60deg, #df2020 10%, #df6020 10%, #df6020 25%, #dfbf20 25%, #dfbf20 40%, #20df40 40%, #20df40 55%, #209fdf 55%, #209fdf 70%, #6020df 70%, #6020df 85%, #df2020 85%);
	color: #fff;
	animation: 10s BeProud linear infinite, 3s Always ease alternate infinite;
}

/* 2. Content ================================= */
section.information {
	width: 100%;
	max-width: 70%;
	margin: 100px auto;
}

section.information .info-section {
	color: #fff;
	font-family: 'Raleway', sans-serif;
	line-height: 1.6;
	text-align: center;
}

section#portfolio.layout.layout-portfolio {
	font-family: sans-serif;
	width: 100%;
	max-width: 80%;
	margin: -20px auto;
	padding: 50px 20px;
	border: 3px dotted white;
}

section#portfolio.layout.layout-portfolio .container {
	width: 95%;
	position: relative;
	margin: 0 auto;
	max-width: 1440px;
}

section#portfolio.layout.layout-portfolio .text-container {
	height: 100%;
	position: relative;
	text-align: center;
	color: #fff;
}

section#portfolio.layout.layout-portfolio .text-container h4 {
	font-family: 'Bowlby One SC', cursive;
	font-size: 1.5rem;
	position: absolute;
	z-index: 1;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -70%);
}

section#portfolio.layout.layout-portfolio .text-container span {
	font-family: 'Fredoka One', cursive;
	font-size: 1.2rem;
	margin-top: 50px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 1;
}

@media (min-width: 992px) {
	section#portfolio.layout.layout-portfolio .text-container span {
		display: none;
	}
}

section#portfolio.layout.layout-portfolio .portfolio-grid {
	padding: 25px 0;
	position: relative;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

section#portfolio.layout.layout-portfolio .portfolio-grid a {
	text-decoration: none;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .single-project {
	width: 100%;
	text-align: center;
	margin: 15px 0;
	position: relative;
	padding: 0 15px;
	height: 275px;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .single-project .inner {
	height: 100%;
	position: relative;
	border-radius: 50%;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .single-project .inner .img-overlay {
	position: absolute;
	z-index: 1;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: rgba(0, 0, 0, 0.3);
	border-radius: 50%;
}

@media (min-width: 992px) {
	section#portfolio.layout.layout-portfolio .portfolio-grid .single-project .inner .img-overlay {
		background: rgba(0, 0, 0, 0.5);
	}
	section#portfolio.layout.layout-portfolio .portfolio-grid .single-project .inner .img-overlay:hover {
		background: rgba(0, 0, 0, 0.2);
	}
}

@media (min-width: 768px) {
	section#portfolio.layout.layout-portfolio .portfolio-grid .single-project {
		width: 50%;
		height: 250px;
	}
}

@media (min-width: 992px) {
	section#portfolio.layout.layout-portfolio .portfolio-grid .single-project {
		width: 33.3%;
		height: 200px;
	}
}

@media (min-width: 1200px) {
	section#portfolio.layout.layout-portfolio .portfolio-grid .single-project {
		width: 25%;
		height: 200px;
	}
}

section#portfolio.layout.layout-portfolio .portfolio-grid .single-project:focus, section#portfolio.layout.layout-portfolio .portfolio-grid .single-project:active {
	outline: 0px;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .single-project:hover .text-container span.project-subtitle {
	display: block;
	animation: fade-in .8s ease-out;
}

@keyframes fade-in {
	0% {
		color: #EF8E20;
		opacity: 0;
	}
	100% {
		color: #fff;
		opacity: 1;
	}
}

@keyframes BeProud {
	100% {
		background-position: 100vw 0px;
	}
}

@keyframes Always {
	100% {
		transform: scale(1.1);
	}
}
